/* Estilos gerais (manter as configurações existentes) */
body {
  font-family: sans-serif;
  text-align: center;
  background: #121212;
  color: #e0e0e0;
  padding: 50px;
}

h1 {
  color: #ffffff;
}

button {
  font-size: 18px;
  padding: 15px 30px;
  margin: 10px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: background 0.3s;
}

#onBtn {
  background-color: #388e3c;
  color: white;
}

#onBtn:hover {
  background-color: #2e7d32;
}

#offBtn {
  background-color: #d32f2f;
  color: white;
}

#offBtn:hover {
  background-color: #c62828;
}

#publishStatus {
  margin-top: 10px;
  font-size: 16px;
  color: #64b5f6;
}

/* Estilo base para o status */
.status-text {
  margin-top: 20px;
  font-size: 18px;
}

/* Cores dinâmicas do status */
.status-connected {
  color: #4caf50;
}

.status-reconnecting {
  color: #ffb74d;
}

.status-error {
  color: #e57373;
}

.status-disconnected {
  color: #e57373;
}

/* Estilos específicos para o formulário de login */
form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  max-width: 400px;
  margin: 0 auto;
  padding: 40px;
  background-color: #1e1e1e;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

input {
  padding: 12px;
  font-size: 16px;
  border: 1px solid #4caf50;
  border-radius: 8px;
  background-color: #121212;
  color: #e0e0e0;
  width: 100%;
  box-sizing: border-box;
}

input::placeholder {
  color: #bdbdbd;
}

input:focus {
  outline: none;
  border-color: #64b5f6;
}

button {
  padding: 12px 20px;
  background-color: #388e3c;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  transition: background 0.3s ease;
}

button:hover {
  background-color: #2e7d32;
}

button:disabled {
  background-color: #bdbdbd;
  cursor: not-allowed;
}

/* Títulos do formulário */
h2 {
  color: #ffffff;
  margin-bottom: 20px;
}